<template lang="en">
<section>
    <div class="my-popups">
        <loader :is-visible="isLoading" />

        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">

                    <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                        <h5 class="modal-title">{{ $t('Manage') }}</h5>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" ref="ClientFlag" :buttons="buttons" :schema="FormSchema" :mandatorytext="$t('MandatoryString')">
                            <template v-slot:tgslot-flagCondition="{data}">
                                <div class="col-lg-12 manage-color">
                                    <div class="row mng-number mb-1 align-items-stretch mx-0 divclone" id="divclone" data-index="0">

                                        <!-- Number Box -->
                                        <div class="col-lg-1 bg-numbr p-3 text-center d-flex align-items-center">
                                            <h2 class="w-100" id="lblClone">1</h2>
                                        </div>

                                        <!-- Content -->
                                        <div class="col-lg-11 p-3">
                                            <div class="row">

                                                <!-- WHEN Header -->
                                                <div class="col-lg-12 mb-2">
                                                    <div class="heading-border mb-2">
                                                        <h5 class="h5">
                                                            <span>When</span>
                                                            <small class="float-right">
                                                                <a href="javascript:;" class="delflagcondition" style="display:none;">
                                                                    <i class="fa fa-trash pr-2"></i>Delete
                                                                </a>
                                                            </small>
                                                        </h5>
                                                    </div>
                                                </div>

                                                <!-- Service Name -->
                                                <div class="col-lg-10">
                                                    <label>Service Name:<span class="mandatory">*</span></label>
                                                    <Field name="ServiceName" :rules="{ required: true }" v-slot="{ errors }">
                                                        <div class="form-group">
                                                            <div class="input-group mb-3">
                                                                <select name="ServiceName" class="form-control" :class="{ 'is-invalid': errors[0] }" v-model="ServiceId" @change="handleTemplateChange(data.fieldInfo.value)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option v-for="(item, idx) in ServiceList" :key="idx" :value="item.SERVICE_ID">
                                                                        {{ item.SERVICENAME }}
                                                                    </option>
                                                                </select>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">
                                                                    {{ errors[0] }}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </Field>
                                                </div>

                                                <!-- Status -->
                                                <div class="col-lg-2">
                                                    <label>Status:<span class="mandatory">*</span></label>
                                                    <Field name="Status" :rules="{ required: true }" v-slot="{ errors }">
                                                        <div class="form-group">
                                                            <div class="input-group mb-3">
                                                                <select name="Status" class="form-control" :class="{ 'is-invalid': errors[0] }" v-model="StatusId" @change="handleTemplateChange(StatusId)">
                                                                    <option value="">{{ $t('Select') }}</option>
                                                                    <option v-for="(item, idx) in StatusData" :key="idx" :value="item.STATUS_ID">
                                                                        {{ item.STATUS }}
                                                                    </option>
                                                                </select>
                                                                <span v-if="errors[0]" class="invalid-feedback d-block">
                                                                    {{ errors[0] }}
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </Field>
                                                </div>

                                                <!-- THEN Section -->
                                                <div class="col-lg-12 divthen">
                                                    <div class="row">

                                                        <div class="col-lg-12 mb-2">
                                                            <div class="heading-border mb-2">
                                                                <h5 class="h5"><span>Then</span></h5>
                                                            </div>
                                                        </div>

                                                        <div class="col-lg-12 divthenchange">
                                                            <div class="row divchange" v-for="(item, index) in Mainleveldiv" :key="index">

                                                                <!-- Change -->
                                                                <div class="col-lg-5">
                                                                    <div class="form-group">
                                                                        <label>Change:<span class="mandatory">*</span></label>

                                                                        <Field :name="'Change_' + index" :rules="{ required: true }" v-slot="{ errors }">
                                                                            <div class="form-group">
                                                                                <div class="input-group mb-3">
                                                                                    <select class="form-control" :class="{ 'is-invalid': errors[0] }" v-model="item.TempId" @change="GetFlagFieldListDropdown(index, item.TempId)">

                                                                                        <option value="">{{ $t('Select') }}</option>
                                                                                        <option v-for="(ch, idx) in ChangeFieldData" :key="idx" :value="ch.TEMPLATE_ID">
                                                                                            {{ ch.DISPLAY_NAME }}
                                                                                        </option>
                                                                                    </select>

                                                                                    <span v-if="errors[0]" class="invalid-feedback d-block">
                                                                                        {{ errors[0] }}
                                                                                    </span>
                                                                                </div>
                                                                            </div>
                                                                        </Field>
                                                                    </div>
                                                                </div>

                                                                <!-- To -->
                                                                <div class="col-lg-5">
                                                                    <div class="form-group">
                                                                        <label>To:<span class="mandatory">*</span></label>

                                                                        <Field :name="'To_' + index" :rules="{ required: true }" v-slot="{ errors }">
                                                                            <div class="form-group">
                                                                                <div class="input-group mb-3">
                                                                                    <select class="form-control" :class="{ 'is-invalid': errors[0] }" v-model="item.ToId" @change="UpdateSelectedToData(item)">

                                                                                        <option value="">{{ $t('Select') }}</option>

                                                                                        <!-- FIX: Use item.ToData -->
                                                                                        <option v-for="(t, idx) in item.ToData" :key="idx" :value="t.VALUE">
                                                                                            {{ t.NAME }}
                                                                                        </option>
                                                                                    </select>
                                                                                    <span v-if="errors[0]" class="invalid-feedback d-block">
                                                                                        {{ errors[0] }}
                                                                                    </span>
                                                                                </div>
                                                                            </div>
                                                                        </Field>
                                                                    </div>
                                                                </div>

                                                                <!-- Buttons -->
                                                                <div class="col-lg-2 mng-btn">
                                                                    <label></label>
                                                                    <div class="input-group">
                                                                        <em class="btn btn-primary mb-2" v-if="index == 0" @click="addRow">
                                                                            <i class="fa fa-plus font-20 mt-1"></i>
                                                                        </em>
                                                                        <em class="btn btn-danger mb-2" v-if="index != 0" @click="deleteRow(index)">
                                                                            <i class="fa fa-trash font-20 mt-1"></i>
                                                                        </em>
                                                                    </div>
                                                                </div>

                                                            </div>

                                                        </div>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>

                </div>
            </div>
        </div>

    </div>
</section>
</template>

<script>
import DataService from '../../services/DataService';


export default {
    props: {
        EditData: {
            type: Array,
            required: false,
        },
    },
    data() {
        return {
            isLoading: false,
            ClientNameData: [],
            StatusData: [],
            FindalLocationArray: [],
            BillRule: [],
            ServiceList: [],
            ServiceId: null,
            ChangeFieldData: [],
            Mainleveldiv: [{
                TempId: [],
                TEMPLATE_ID: "",
                ToId: [],
                NAME: '',
                VALUE: null,
                ToData: []
            }],
            PageSize: 100,
            PageNumber: 1,
            searchCondition: '',
            GroupNewData: [],
            FormLabel: 'Automate Flag',
            StatusId:null,
            setupId: null,            
            isShowUnit: true,
            isGroupBy: false,
            isComboChargeScope: false,
            FormSchema: [{
                    layoutType: "double",
                    Data: [

                         {
                            astype: "SelectField",
                            label: this.$t('SelectClient'),
                            name: "Client",
                            value: "",
                            placeholder: this.$t('SelectClient'),
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.GetBillingRuleByClient

                            },
                            validationRules: {
                                required: true
                            },

                        },

                        // {
                        //     astype: "MultiSelectField",
                        //     label: this.$t('SelectClient'),
                        //     name: "Client",
                        //     value: "",
                        //     mode: "tag",
                        //     placeholder: "",
                        //     config: {
                        //         options: [],
                        //         onChange: this.GetBillingRuleByClient,
                        //         onRemove: this.removeRecord,
                        //         // onRemove($event, field, meta)
                        //     },
                        //     validationRules: {
                        //         "required": true
                        //     },
                        // },

                        // {
                        //     astype: "TreeSelectField",
                        //     label: this.$t('SelectClient'),
                        //     name: "Client",
                        //     value: null,
                        //     placeholder: this.$t('SelectClient'),
                        //     disabled: false,
                        //     config: {
                        //         options: [],
                        //        // onChange: this.GetBillingRuleByClient
                        //     },
                        //     validationRules: {
                        //         "required": true
                        //     },
                        // },
                        {
                            astype: "SelectField",
                            label: this.$t('BillingRuleName'),
                            name: "RuleName",
                            value: "",
                            placeholder: this.$t('BillingRuleName'),
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.GetFlagFrom

                            },
                            validationRules: {
                                required: true
                            },

                        }

                    ]
                },
                //     {
                //         group_name: "Flag Condition",
                //         layoutType: "double",
                //         Data: [
                //                 {
                //                     astype: "SelectField",
                //                     label: this.$t('Service'),
                //                     name: "Service",
                //                     value: "",
                //                     visibility: false,
                //                     placeholder: "Service",
                //                     disabled: false,
                //                     config: {
                //                         options: [],
                //                     },
                //                     validationRules: {
                //                         "required": true
                //                     },
                //                 },                           
                //                 {
                //                     astype: "SelectField",
                //                     label: this.$t('Status'),
                //                     name: "Status",
                //                     value: "",
                //                     visibility: false,
                //                     placeholder: "Status",
                //                     config: {
                //                         options: [],
                //                     },
                //                 },                       
                //         ]
                //  },
                {
                    group_name: "Flag Condition",
                    layoutType: "single",
                    Data: [{
                        astype: "SlotField",
                        name: "flagCondition",
                        value: "",
                        visibility: false,
                        config: {
                            options: [],
                            onChange: this.InvoiceComponentFieldsValues,
                        },
                        //validationRules: { "required": true, max: 100 },
                    }, ]
                }

            ],
            buttons: [{
                    type: "button",
                    class: "btn btn-primary",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('AddAdditionalFlagCondition'),
                    onClick: this.AddAdditionalFlag
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>" + this.$t('SaveRule'),
                    onClick: this.SaveData
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>" + this.$t('Cancel'),
                    onClick: this.Close,
                },
            ],
        };
    },

    created: async function () {
        debugger; // eslint-disable-line no-debugger
        var vm = this;      
        vm.GetStatusList();
        vm.GetParentClientGroupNew();
        if(vm.EditData.length >0 && vm.EditData[0].CLIENT_FLAG_ID>0){
            debugger
            setTimeout(() => {
                (vm.FormSchema)[0].Data[0].value=vm.EditData[0].CLIENT_ID;
            }, 300);
            
            vm.GetBillingRuleByClient('client','',vm.EditData[0].CLIENT_ID);
            setTimeout(() => {
                vm.GetFlagFrom();
                (vm.FormSchema)[0].Data[1].value = vm.EditData[0].CONFIGURATION_ID;

                // Ensure Mainleveldiv has same number of rows as EditData
                                vm.Mainleveldiv = vm.EditData.map(() => ({
                                    TempId: [],
                                    TEMPLATE_ID: "",
                                    ToId: [],
                                    NAME: '',
                                    VALUE: null,
                                    ToData: []
                                }));
                setTimeout(() => {
                    vm.ServiceId=vm.EditData[0].SERVICE_ID;
                    vm.StatusId=vm.EditData[0].STATUS_ID;

                    setTimeout(() => {
                        vm.GetFieldsListBySetupId();
                        setTimeout(() => {                         

                                vm.EditData.forEach((item, index) => {
                                    setTimeout(() => {
                                        debugger;
                                        vm.Mainleveldiv[index].TempId=(parseInt(item.FIELD_NAME));
                                        vm.GetFlagFieldListDropdown(index, parseInt(item.FIELD_NAME));
                                        
                                            setTimeout(() => {
                                                vm.Mainleveldiv[index].ToId=(item.FIELD_VALUE);
                                            }, 500);  

                                    }, 200);
                                                                     
                                });

                        }, 200);                       
                    }, 300);

                }, 100);                
            }, 100);
        
        }
    },

    methods: {
        Close() {
            this.$emit('CloseAddFlag');
        },
        AddAdditionalFlag() {
            var vm=this;
            vm.ShowAlert(vm.$t('Nomoreflagconditionexist', [vm.$t('Flag')]), "warning", true, vm.$t("Alert")); 
        },

        addRow() {
            this.Mainleveldiv.push({
                TempId: [],
                TEMPLATE_ID: "",
                ToId: [],
                NAME: "",
                VALUE: null,
                ToData: []
            });
        },

        deleteRow(index) {
            if (this.Mainleveldiv.length > 1) {
                this.Mainleveldiv.splice(index, 1);
            }
        },

        handleTemplateChange() {

        },

        GetBillingRuleByClient: function (event, field, meta) { 
            debugger;          
            var vm = this;
            //var url = `clientId=${evt.target.value}`
            if(event=='client'){
                var url = `clientId=${meta}`
            }else{
                var url = `clientId=${field.value}`
            }            
            DataService.GetBillingRuleByClient(url).then((response) => {               
                if (response.data.result.length > 0) {
                    (vm.FormSchema)[0].Data[1].config.options = [];
                    vm.BillRule = response.data.result;
                    vm.BillRule.forEach((itm, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            name: `${itm.BILLING_CONFIG_NAME}`,
                            value: `${itm.CONFIGURATION_ID}`
                        })
                    })
                    vm.$refs.ClientFlag.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false
            });
        },
        GetFlagFrom: function () {
            var vm = this;
            (vm.FormSchema)[1].Data[0].visibility = true;
            // (vm.FormSchema)[1].Data[1].visibility = true;
            vm.GetServiceList();
            vm.GetStatusList();
            vm.GetFieldsListBySetupId();
        },
        GetServiceList: function () {
            var vm = this;
            DataService.GetServiceList().then((response) => {
                if (response.data.result[0].length > 0) {
                    vm.ServiceList = response.data.result[0];
                    vm.ServiceList.forEach((itm, index) => {
                        (vm.FormSchema)[1].Data[0].config.options.push({
                            name: itm.SERVICENAME,
                            value: itm.SERVICE_ID
                        })
                    })
                    vm.$refs.ClientFlag.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false
            });
        },
        GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            try {
                DataService.GetClientStatusList().then((response) => {
                    if (JSON.parse(response.data) != null) {
                        vm.StatusData = JSON.parse(response.data);                       
                        vm.isLoading = false;
                    }
                    vm.isLoading = false;
                });
            } catch (error) {
                vm.isLoading = false;
            }
        },

        // async GetCompanyParentClient() {
        //     var vm = this;
        //     await DataService.GetCompanyParentClient().then(response => {
        //         vm.ClientNameData = response.data.result[0];

        //         vm.FindalLocationArray = [];
        //         vm.ClientNameData.forEach((value) => {
        //             var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
        //                 return el.VALUE == value.VALUE;
        //             });
        //             if (CheckIfExist.length == 0) {
        //                 if (value.PARENT_ID == null && value.NAME != null) {
        //                     let locationId = value.VALUE
        //                     let rslt = vm.getChildLocation(locationId, value, response.data.result[0])
        //                     vm.FindalLocationArray.push(rslt)
        //                 }
        //             }
        //         });
        //         (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
        //         //vm.options = vm.FindalLocationArray;
        //     })
        //     vm.$refs.ClientFlag.UpdateKeyValue();
        // },
        GetParentClientGroupNew() {
            debugger
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&reqFrom='CUSTOM'&search=${vm.searchCondition}&userId=${vm.GetUserInfo.ID}`;

            DataService.GetParentClientGroupNew(url).then((response) => {
                vm.ClientNameData = vm.convertApiTreeToFlatList(response.data.result);
                vm.ClientNameData.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[0].config.options.push({
                        name: itm.CHILD_NAME,
                        value: itm.CHILD_USER_ID
                    })
                    //if(vm.EditData[0].length>0){
                      //  (vm.FormSchema)[0].Data[0].value=vm.EditData[0].CLIENT_ID;
                    //}
                })
                vm.$refs.ClientFlag.UpdateKeyValue();
            });
        },   

        convertApiTreeToFlatList(tree) {
            let flat = [];

            function walkParent(node) {
                // Push parent
                flat.push({
                    CHILD_USER_ID: node.parenT_ID,
                    CHILD_NAME: node.parenT_NAME,
                    PARENT_ID: null
                });

                // Push children
                if (node.children && node.children.length > 0) {
                    node.children.forEach(child => {
                        flat.push({
                            CHILD_USER_ID: child.chilD_USER_ID,
                            CHILD_NAME: child.chilD_NAME,
                            PARENT_ID: node.parenT_ID
                        });
                    });
                }
            }

            tree.forEach(root => walkParent(root));

            return flat;
        },      

        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.CHILD_USER_ID,
                label: currentItem.CHILD_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.parenT_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.CHILD_USER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.CHILD_USER_ID == value.CHILD_USER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.parenT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) { //&& CheckIfExist.NAME !=null
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.CHILD_USER_ID}`,
                            label: `${value.CHILD_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

        GetFieldsListBySetupId: function () {
            var vm = this;
            vm.isLoading = true;
            var configid = parseFloat(vm.FormSchema[0].Data[1].value);
            var selectedRule = vm.BillRule.find(x => x.CONFIGURATION_ID == configid);
            vm.setupId = selectedRule ? selectedRule.SETUP_ID : null;

            // If no setupId is found  reset and exit
            if (!vm.setupId) {
                vm.ChangeFieldData = [];
                vm.isLoading = false;
                return;
            }

            var url = `setupId=${vm.setupId}&isShowUnit=${vm.isShowUnit}&isGroupBy=${vm.isGroupBy}&isComboChargeScope=${vm.isComboChargeScope}`;

            DataService.GetFieldsListBySetupId(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.ChangeFieldData = JSON.parse(response.data);

                    // reset Change selections for all rows
                    vm.Mainleveldiv.forEach(i => {
                        i.TempId = "";
                        i.ToData = [];
                        i.ToId = "";
                        i.VALUE=null;
                        i.NAME='';
                    });

                    vm.$refs.ClientFlag.UpdateKeyValue();
                }

                vm.isLoading = false;
            });
        },

        GetFlagFieldListDropdown(index, item) {
            debugger;
            const vm = this;
            const rule = vm.ChangeFieldData.find(x => x.TEMPLATE_ID == item);
            const name = rule ? rule.DISPLAY_NAME : null;

            vm.isLoading = true;

            DataService.GetFlagFieldListDropdown(`reqFrom=${name}`).then(res => {
                vm.Mainleveldiv[index].ToData = JSON.parse(res.data);
                vm.Mainleveldiv[index].ToId = "";
                vm.Mainleveldiv[index].NAME = "";
                vm.Mainleveldiv[index].VALUE = "";
                vm.isLoading = false;
            });
        },

        UpdateSelectedToData(item) {
            const selected = item.ToData.find(t => t.VALUE == item.ToId);
            if (selected) {
                item.NAME = selected.NAME;
                item.VALUE = selected.VALUE;
            } else {
                item.NAME = "";
                item.VALUE = "";
            }
        },

        async SaveData() {
            var vm = this;
            const result = await vm.$refs.ClientFlag.validateForm.validate();
            if (result) {
                debugger;
                var objData = {
                    ClientFlagId: vm.EditData.length > 0 ? vm.EditData[0].CLIENT_FLAG_ID:0,
                    ClientId: vm.FormSchema[0].Data[0].value || 0,
                    RoleId: vm.FormSchema[0].Data[1].value || 0,
                    ServiceId: vm.ServiceId || 0,
                    SetupId: vm.setupId || 0,
                    //Status: vm.StatusId || 1001,
                    Mainleveldiv: vm.Mainleveldiv
                };
                debugger;
                var obj = JSON.stringify(objData);
                var params = `objData=${encodeURIComponent(obj)}`;
                await DataService.SaveUpdateFlagData(obj).then(response => {                    
                    if (response.data != null) {
                        const Result=JSON.parse(response.data)[0];
                        debugger
                        if (Result.Success == 1) {                            
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Flag')]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        } else if (Result.Success == 2) {
                             vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Flag')]), "Success", true, vm.$t('Alert'));
                             vm.Close();
                        }
                        else if (Result.Success == 0 && Result.Error== 'EXISTS') {
                            vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Flag')]), "warning", true, vm.$t("Alert"));                            
                        } else {
                            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                        }
                    }
                });

            }
        },

    }
};
</script>
