<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="ClientFlag" SubModuleCode="AutomatedFlagProcessingBilling" 
    IdentityColumn="CLIENTFLAGID" :HeaderText="$t('AutomateFlagProcessing')" :ListData="ListingData" :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" 
    :TotalRecords="TotalRecord" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="RuleFlagListing" :SortExp="SortExp" :SortBy="SortBy" :SearchFields="leftSearchFields"
    IsSearchFieldsOnTop="true" SearchPosition="top"  :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
     v-on:SortdataButtonClick="sortdata"   :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:ActionButtonClick="actionButtonClick">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'FLAGCOUNT'">
                <span>
                    <em  :class="{ 'text-dark text-center single-action': true, 'disabled': (data.row.FLAGCOUNT == 0) }" href="javascript:;" v-on:click="OpenBatchProcessDataLising(data.row.batch_id)">
                        <em class="fa fa-flag-o" aria-hidden="true"></em>
                        <span class="noti-circle noti-green bg-info">{{ data.row.FLAGCOUNT }}</span>
                    </em>
                </span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'STATUSID'">
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <select v-model="data.row.STATUSID" @change="UpdataStatus(data.row.STATUSID, data.row.CLIENTFLAGID);" :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                            <option value="1001" selected="selected">{{ $t("Active") }}</option>
                            <option value="1002">{{ $t("Inactive") }}</option>
                        </select>
                    </div>
                </td>
            </template>
        </template>
    </tg-list>
    <FlagModel v-if="IsAddFlag" @CloseAddFlag="CloseAddFlag" :EditData="EditData"> </FlagModel>
</section>
</template>

<script>
import FlagModel from '../../components/Modals/ClientFlagModel.vue'
import DataService from '../../services/DataService';
export default {
    components: {
        FlagModel
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecord: 0,
            ListingData: [],
            IsAddFlag: false,
            IsStepDetails: false,
            StepDetailsObj: null,
            searchCondition: '',
            Headers: [{
                    "COLUMN_NAME": "CLIENTNAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ClientName'),
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "RULENAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('BillingRuleName'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "FLAGCOUNT",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('FlagCount'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": "CREATEDBYNAME",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CreatedBy'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "STATUSID",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    },
                },

            ],
            listActions: [{
                title: 'Edit',
                icon: 'fa fa-edit text-success action-icon',
                additionalSpan: '',
                callbackfunction: 'EditFlag'
            }, ],
            Buttons: [{
                    title: this.$t('AddNew'),
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddNew',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                },
                {
                    title: this.$t('Active'),
                    iconClass: 'fa fa-check',
                    callbackfunction: 'Active',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    isEnabledOnSelectedRow: true
                },
                {
                    title: this.$t('InActive'),
                    iconClass: 'fa fa-close',
                    callbackfunction: 'InActive',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    isEnabledOnSelectedRow: true
                },
                {
                    title: this.$t('Delete'),
                    iconClass: 'fa fa-trash',
                    callbackfunction: 'Delete',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    isEnabledOnSelectedRow: true
                }
            ],
              leftSearchFields: [{
                fieldName: this.$t('Search By Condition Name'),
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            EditData: []
        }
    },
    created: function () {
        var vm = this;
        vm.UserId = vm.GetUserInfo.ID;
        vm.CompanyId = vm.GetUserInfo.CompanyId;
        
        // vm.Headers.forEach(element => {
        //     element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME)
        // })
        vm.RuleFlagListing();
    },
    methods: {
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'EditFlag':
                        action.isVisible = true;
                        this.EditFlag(actionItem);
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.RuleFlagListing();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'EditFlag':
                    this.EditFlag(item);
                    break;
            }
        },
         leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchCondition) {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            } else {
                this.searchCondition = "";
            }
              this.RuleFlagListing();
        },
        commonHeaderButtonClick: async function (event) {
            var vm = this;
            switch (event.callbackfunction) {
                case 'AddNew':
                    vm.AddNew();
                    break;
                case 'Active':
                    this.UpdataStatus(1001, '');
                    break;
                case 'InActive':
                    this.UpdataStatus(1002, '');
                    break;
                case 'Delete':
                    this.commonDeleteData();
                    break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
              vm.RuleFlagListing();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        AddNew() {
            var vm = this;
            vm.IsAddFlag = true;
        },
        CloseAddFlag() {
            var vm = this;
            vm.EditData=[];
            vm.IsAddFlag = false;
            vm.RuleFlagListing();
        },
        RuleFlagListing() {
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.searchCondition}`;
            DataService.RuleFlagListing(url).then((response) => {
                vm.isLoading = false;
                if (response.data.result.length > 0) {
                    vm.ListingData = response.data.result;
                    vm.ListingData.forEach((item) => {
                        item.created_at = new Date(item.created_at)
                    })
                    vm.TotalRecord = response.data.result.TOTALRECORDS;
                    vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.Buttons[1].isdisabled = true;
                    vm.Buttons[2].isdisabled = true;
                    vm.Buttons[3].isdisabled = true;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecord = 0;
                }
            })
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "ClientFlag",
                        subModuleCode: "",
                        refCode: "TALYGEN_BE_CLIENT_FLAG_MASTER"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonDeleteData(objectPostString).then(async function (response) {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            $("#DeleteMultiple").addClass("disabled");
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Flag')]), "success", true, vm.$t("Alert"));
                            vm.PageNumber = parseInt(vm.PageNumber) - 1;
                            if (vm.PageNumber <= 0) {
                                vm.PageNumber = 1;
                            }
                            await vm.RuleFlagListing();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Flag')]), "failure", true, vm.$t("Alert"));
                        }

                    },);
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        UpdataStatus(StatusID, ID) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(async function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });

            vm.confirmR(vm.$t('AreYouSureToUpdateStatus'), true, false, function (result) {
                var postJSON = {
                    ID: val || ID,
                    StatusId: StatusID,
                    IsFor: "TALYGEN_BE_CLIENT_FLAG_MASTER",
                    Module: "ClientFlag",
                    Submodule: "AutomatedFlagProcessingBilling",
                };
                var objectPostString = JSON.stringify(postJSON);
                DataService.CommonUpdateStatus(objectPostString).then(async response => {
                    if (response.status == 200) {
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vm.listheaderbuttons[1].isdisabled = true;
                        vm.listheaderbuttons[2].isdisabled = true;
                        vm.listheaderbuttons[3].isdisabled = true;
                        vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                        vm.RuleFlagListing();

                    }
                });
            })
        },
        async EditFlag(item) {     
            var vm = this;
            vm.isLoading = true;
            try {
                    var url = `flagId=${item.CLIENTFLAGID}`
                    await DataService.GetFlagRecordByFlagId(url).then((response) => {               
                        vm.IsAddFlag = true;
                        vm.EditData=JSON.parse(response.data);                   
                        vm.isLoading = false;
                    });
            } catch (error) {
                vm.isLoading = false;
            }
        },
    }
}
</script>

<style scoped>
.task-dec {
    color: #007bff !important;
    text-decoration: none;
    background-color: transparent;
}
</style>
